import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { getDataSimple } from '../redux/actions/table'
import { getWhere, getWhereDara } from '../component/Where/index'
import * as tableActions from '../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import { mainWindow } from '../App'

const width = mainWindow.current.offsetWidth>800? 500: 240;
const width1 = mainWindow.current.offsetWidth>800? 240: 120;
const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width1
        },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: 'calc((100% - '+width+'px)/2)',
        marginRight: 'calc((100% - '+width+'px)/2)'
    },
});

const Event = React.memo(
    (props) =>{
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data } = props.table;
        const { classes } = props;
        const { status } = props.user;

        useEffect(async()=>{
            if (!(status.status==='active'&&['admin', 'manager'].includes(status.role))) {
                props.history.push('/')
            }
            await setCities(await getDataSimple({name: 'Города'}));
            await setWheres(await getDataSimple({name: 'ГдеID', data: {city: city}}));
        },[])
        let [whereData, setWhereData] = useState(selected!==-1?JSON.parse(data[selected][6]).data:{});
        let [whereComponent, setWhereComponent] = useState(null);
        let [nameRu, setNameRu] = useState(selected!==-1?data[selected][1]:'');
        let handleNameRu = (event) => {
            setNameRu(event.target.value)
        };
        let [descriptionRu, setDescriptionRu] = useState(selected!==-1?data[selected][2]:'');
        let handleDescriptionRu = (event) => {
            setDescriptionRu(event.target.value)
        };
        let [nameKg, setNameKg] = useState(selected!==-1?data[selected][3]:'');
        let handleNameKg = (event) => {
            setNameKg(event.target.value)
        };
        let [descriptionKg, setDescriptionKg] = useState(selected!==-1?data[selected][4]:'');
        let handleDescriptionKg = (event) => {
            setDescriptionKg(event.target.value)
        };
        let populars = ['on', 'off'];
        let [popular, setPopular] = useState(selected!==-1?data[selected][12]:'off');
        let handlePopular = (event) => {
            setPopular(event.target.value)
        };
        let actives = ['on', 'off'];
        let [active, setActive] = useState(selected!==-1?data[selected][13]:'on');
        let handleActive = (event) => {
            setActive(event.target.value)
        };
        let [cities, setCities] = useState([]);
        let [city, setCity] = useState(selected!==-1?data[selected][5]:'');
        let handleCity = async(event) => {
            setCity(event.target.value)
            setWheres(await getDataSimple({name: 'ГдеID', data: {city: event.target.value}}));
        };
        let [wheres, setWheres] = useState([]);
        let [whereId, setWhereId] = useState(undefined);
        let [where, setWhere] = useState(selected!==-1?JSON.parse(data[selected][6]).name:'');
        let handleWhere = async(event) => {
            await setWhere(event.target.value);
            let _data = {}
            for(let i = 0; i<dates.length; i++){
                _data[dates[i]] = await getWhereDara(event.target.value);
            }
            let _date = date
            if(date===''){
                await setDate(dates[0]);
                _date = dates[0]
            }
            await setWhereData(_data)
            await setWhereComponent(await getWhere(event.target.value, _data, setWhereData, prices, _date, false, undefined, selected));
        };
        let [addDate, setAddDate] = useState('');
        let handleAddDate = (event) => {
            setAddDate(event.target.value)
        };
        let [date, setDate] = useState('');
        let handleDate = async (event) => {
            setDate(event.target.value)
            if(event.target.value===''){
                handleWhere(event);
            } else {
                setWhereComponent(await getWhere(where, whereData, setWhereData, prices, event.target.value, false, undefined, selected));
            }
        };
        let [dates, setDates] = useState(selected!==-1?JSON.parse(data[selected][7]):[]);
        let [prices, setPrices] = useState(selected!==-1?JSON.parse(data[selected][8]):[]);
        let [video, setVideo] = useState(selected!==-1?data[selected][9]:'');
        let handleVideo = (event) => {
            setVideo(event.target.value)
        };
        let [ages, setAges] = useState(selected!==-1?data[selected][10]:'');
        let handleAges = (event) => {
            setAges(event.target.value)
        };
        let [colors, setColors] = useState(['green', 'yellow', 'violet', 'orange', 'indigo', 'lime', 'sienna', 'darkorange', 'purple', 'indigo', 'blue', 'deeppink']);
        let [addColor, setAddColor] = useState('');
        let handleAddColor = (event) => {
            setAddColor(event.target.value)
        };
        let [addPrice, setAddPrice] = useState('');
        let handleAddPrice = (event) => {
            setAddPrice(event.target.value)
        };
        let genres = ['Концерты', 'Развитие', 'Детям', 'Спорт', 'Туризм']
        let [genre, setGenre] = useState(selected!==-1?data[selected][11]:'');
        let handleGenre = (event) => {
            setGenre(event.target.value)
        };
        let [file, setFile] = useState([]);
        let [fileNames, setFileNames] = useState(selected!==-1?data[selected][0]:'');
        let handleChangeFile = ((event) => {
            setFile(event.target.files)
            let fileNames='';
            for(let i=0; i<event.target.files.length; i++){
                if(i!==0)
                    fileNames+=', '
                fileNames+=event.target.files[i].name+','
            }
            setFileNames(fileNames)
        })
        return (
            <div>
                <TextField
                    label='имя'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameRu}
                    onChange={handleNameRu}
                />
                <br/>
                <TextField
                    multiline
                    rowsMax='4'
                    label='описание'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={descriptionRu}
                    onChange={handleDescriptionRu}
                />
                <br/>
                <TextField
                    label='ысым'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={nameKg}
                    onChange={handleNameKg}
                />
                <br/>
                <TextField
                    multiline
                    rowsMax='4'
                    label='баяндоо'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={descriptionKg}
                    onChange={handleDescriptionKg}
                />
                <br/>
                {selected!==-1?
                    <>
                    <br/>
                    <b>Город</b>
                    <br/>
                    <br/>
                    {city}
                    </>
                    :
                    <TextField
                        select
                        SelectProps={{
                            MenuProps: {
                                className: classes.menu,
                            },
                        }}
                        label='город'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={city}
                        onChange={handleCity}
                    >
                        {cities != undefined?
                            cities.map(option => (
                                <MenuItem key={option} value={option}>
                                    {option}
                                </MenuItem>
                            ))
                            :
                            null
                        }
                    </TextField>
                }
                <br/>
                      <br/>
                <b>Даты</b>
                <br/>
                <div className={classes.message}>
                    {dates.map((element, idx) => {return <>{element}{selected===-1?<IconButton className={classes.button} onClick={ async()=>{
                        let _dates = dates;
                        let _whereData = whereData;
                        await delete _whereData[dates[idx]]
                        await setWhereData(_whereData)
                        await _dates.splice(idx, 1);
                        if(_dates.length>0){
                            await handleDate({target: {value: _dates[0]}})
                        } else
                            await handleDate({target: {value: ''}})
                        await setDates(_dates);
                     }} aria-label='Delete'><DeleteIcon /></IconButton>:null}<br/></>})}
                </div>
                <TextField
                    label='дата'
                    type='datetime-local'
                    className={classes.textField}
                    margin='normal'
                    value={addDate}
                    onChange={handleAddDate}
                />
                <br/>
                <Button variant='contained' color='primary' onClick={async ()=>{
                    console.log(addDate,addDate!=='', !dates.includes(addDate))
                    if(addDate!==''&&!dates.includes(addDate))
                    {let _dates=dates;if(where!==''){let _whereData = whereData; _whereData[addDate] = await getWhereDara(where); setWhereData(_whereData);}_dates.push(addDate);await setDates(_dates);await setAddDate('')}}} className={classes.button}>
                    Добавить дату
                </Button>
                <br/>
                <br/>
                <b>Цены</b>
                <br/>
                <div className={classes.message}>
                    {prices.map((element, idx) => {return <>{JSON.stringify(element)}{selected===-1?<IconButton className={classes.button} onClick={()=>{
                        let _prices = prices; let _colors = colors; _colors.push(_prices[idx].color);setColors(_colors); _prices.splice(idx, 1); setPrices(_prices);
                        handleWhere({target: {value: where}})
                    }} aria-label='Delete'><DeleteIcon /></IconButton>:null}<br/></>})}
                </div>
                <TextField
                    label='цена'
                    type='login'
                    className={classes.textFieldSmall}
                    margin='normal'
                    value={addPrice}
                    onChange={handleAddPrice}
                />
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='цвет'
                    type='login'
                    className={classes.textFieldSmall}
                    margin='normal'
                    value={addColor}
                    onChange={handleAddColor}
                >
                    {colors.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                     }
                </TextField>
                <br/>
                <Button variant='contained' color='primary' onClick={async ()=>{if(addPrice!==''&&addColor!==''){let _colors = colors; _colors.splice(_colors.indexOf(addColor), 1);setColors(_colors); let _prices=prices;_prices.push({price: addPrice, color: addColor});await setPrices(_prices);await setAddPrice('');await setAddColor('')}}} className={classes.button}>
                    Добавить цену
                </Button>
                <br/>
                {prices.length>0&&dates.length>0?
                    <>
                    <br/>
                    <b>Где</b>
                    <br/>
                    <br/>
                    {selected!==-1?
                        where
                        :
                        <TextField
                            select
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            label='где'
                            type='login'
                            className={classes.textField}
                            margin='normal'
                            value={where}
                            onChange={handleWhere}
                        >
                            {wheres != undefined?
                                wheres.map(option => (
                                    <MenuItem key={option.nameRu} value={option.nameRu}>
                                        {option.nameRu}
                                    </MenuItem>
                                ))
                                :
                                null
                            }
                        </TextField>
                    }
                    <br/>
                    <br/>
                    {dates.length>0?
                        <TextField
                            select
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            label='дата'
                            type='login'
                            className={classes.textField}
                            margin='normal'
                            value={date}
                            onChange={handleDate}
                        >
                            {
                                dates.map(option => (
                                    <MenuItem key={option} value={option}>
                                        {option}
                                    </MenuItem>
                                ))
                            }
                        </TextField>
                        :
                        null
                    }
                    <br/>
                    {whereComponent}
                    </>
                    :
                    null
                }
                <br/>
                <TextField
                    label='видео'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={video}
                    onChange={handleVideo}
                />
                <br/>
                <TextField
                    label='возраста'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={ages}
                    onChange={handleAges}
                />
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='жанры'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={genre}
                    onChange={handleGenre}
                >
                    {genres != undefined?
                        genres.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='популярный'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={popular}
                    onChange={handlePopular}
                >
                    {
                        populars.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='активный'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={active}
                    onChange={handleActive}
                >
                    {
                        actives.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                    }
                </TextField>
                <br/>
                <div className={classes.message}>
                    {fileNames}
                </div>
                <br/>
                <label htmlFor='contained-button-file'>
                    <Button variant='contained' color={fileNames.length>0?'primary':''} component='span' className={classes.button}>
                        Загрузить изображение
                    </Button>
                </label>
                <br/>
                <div>
                    <Link className='link' to={''} onClick={()=>{
                        if(selected===-1)
                            addData({search: '', sort: '', page: 0, name: 'Событие', file: file, data: {nameRu: nameRu, popular: popular, active: active, nameKg: nameKg, city: city, descriptionRu: descriptionRu, descriptionKg: descriptionKg, where: {name: where, data: whereData}, price: prices, date: dates, video: video, ageCategory: ages, genre: genre}});
                        else
                            setData({id: data[selected][15], search: '', sort: '', page: 0, name: 'Событие', oldFile: data[selected][0], file: file, data: {active: active, popular: popular, nameRu: nameRu, nameKg: nameKg, city: city, descriptionRu: descriptionRu, descriptionKg: descriptionKg, where: {name: where, data: whereData}, price: prices, date: dates, video: video, ageCategory: ages, genre: genre}});
                        setSelected(-1)
                    }}>
                        <Button variant='contained' color='primary' className={classes.button}>
                            Сохранить
                        </Button>
                    </Link>
                    <Link className='link' to={''} onClick={()=>{setSelected(-1);}}>
                        <Button variant='contained' color='secondary' className={classes.button}>
                            Отмена
                        </Button>
                    </Link>
                </div>
                <input
                    accept='image/*'
                    style={{ display: 'none' }}
                    id='contained-button-file'
                    type='file'
                    onChange={handleChangeFile}
                />
                <br/>
            </div>
        );
  })

function mapStateToProps (state) {
    return {
        user: state.user,
        table: state.table,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Event.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Event));
